/* NAVBAR DROPDOWN SUB END */
/* NAVBAR MENU END */
/* HEADER END */

/* SLIDER START */

.slider {
	width: 100%;
	height: var(--slider-height);
	background-color: rgb(236, 236, 236);
	position: relative;
	display: flex;
	overflow: hidden;
	box-shadow: 2px 0 5px rgba(0, 0, 0, 0.4);
}

/* SLIDER END */

/* BODY START  */

/* PRIORITY START  */
.priority {
	width: 100%;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 15px;
	user-select: none;
	padding: 40px 0;
}

.priority article {
	padding: 20px 0px;
	font-family: "bpg banner caps";
	display: flex;
	align-items: center;
	justify-content: center;
	/* flex-direction: row; */
	color: var(--bg-darkgray);
	/* background-color: #ccc;
    border-radius: 8px; */
	transition: 0.5s;
}

.priority article:hover {
	background-color: #e5e5e5;
	border-radius: 8px;
}

.priority article span {
	padding: 10px;
	background-color: #333;
	color: var(--bg-lightsilver);
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 10px;
	border-radius: 5px;
	font-size: var(--fs-2);
}

/* PRIORITY END */

/* PRODUCTIONS START */

.productions-container {
	width: 100%;
	margin: 50px 0;
	display: flex;
	flex-direction: column;
	user-select: none;
}

.productions-container h1 {
	text-align: center;
	font-family: "bpg banner caps";
	margin-bottom: 45px;
	letter-spacing: 2px;
	color: var(--c-darkgray);
}

.productions {
	width: 100%;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 20px;
}

.productions article {
	display: flex;
	align-items: center;
	flex-direction: column;
	background-color: white;
	/* border: 1px solid var(--bg-lightersilver); */
	box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.2);
	padding: 0 10px;
	transition: 0.3s;
	-webkit-transition: 0.3s;
}

.productions article:hover {
	box-shadow: 4px 5px 5px rgba(0, 0, 0, 0.2);
}

.pa-name {
	padding: 25px 20px 5px 20px;
	font-family: "bpg banner caps";
}

.pa-icon {
	font-size: var(--fs-1);
	margin-top: 5px;
	color: var(--c-darkgray);
}

.pa-description {
	max-height: 250px;
	min-height: 250px;
	padding: 20px;
	letter-spacing: 0.045rem;
	line-height: 1.16rem;
	font-size: var(--fs-4);
	display: flex;
	overflow: auto;
	font-family: "bpg glaho web caps";
}

.pa-description::-webkit-scrollbar {
	width: 3px;
}

.pa-description::-webkit-scrollbar-track {
	background-color: #bbb;
}

.pa-btn {
	padding: 10px 40px;
	background-color: rgb(36, 83, 172);
	border: none;
	border-radius: 5px;
	margin-top: 26px;
	margin-bottom: 26px;
	cursor: pointer;
	font-size: var(--fs-3);
	color: var(--bg-lightsilver);
	font-family: "bpg glaho web caps";
	transition: 0.3s;
	-webkit-transition: 0.3s;
}

.pa-btn:hover {
	background-color: rgb(26, 68, 145);
}

/* PRODUCTIONS END */

/* PRODUCTIONS PAGE START  */

.productions-page {
	width: 100%;
	padding: 40px 0;
	user-select: none;
}

.productions-page h1 {
	text-align: center;
	font-family: "bpg banner caps";
	margin-bottom: 50px;
}

.pp-products {
	width: 100%;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 20px;
	flex-direction: column;
}

/* .pp-products a article {
} */

.pp-products a article {
	width: 100%;
}

.pp-products a article h3 {
	color: var(--c-darkgray);
	text-align: center;
	font-family: "bpg banner caps";
	margin-bottom: 20px;
}

.pp-products a article img {
	width: 100%;
	display: block;
	margin-left: auto;
	margin-right: auto;
	/* filter: drop-shadow(2px 2px 3px rgba(0, 0, 0, 0.7)); */
	transition: 0.2s;
	-webkit-transition: 0.1s;
}

.pp-products a article img:hover {
	filter: drop-shadow(3px 3px 0px rgba(5, 47, 94, 1));
}

/* PRODUCTIONS PAGE END */

/* BODY END */

/* FOOTER START */
.footer {
	width: 100%;
	min-height: 350px;
	position: relative;
	background-color: var(--bg-lightsilver);
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	box-shadow: 0px -2px 5px rgba(0, 0, 0, 0.2);
	user-select: none;
}

.footer article {
	display: flex;
	flex-direction: column;
	padding: 30px;
	border-right: 1px solid var(--bg-silver);
	font-family: "bpg banner caps";
	color: var(--c-darkgray);
}

.footer-name {
	width: 100%;
	margin-bottom: 60px;
}

.footer-content {
	padding-left: 40px;
}

.footer-content ul {
	list-style-type: none;
}

.footer-content ul li {
	margin-bottom: 25px;
}

.footer-content ul li a,
.footer-content ul li {
	display: flex;
	font-size: var(--fs-3);
	font-family: "bpg glaho web caps";
	color: var(--c-darkgray);
}

.footer-content ul li a div,
.footer-content ul li div {
	width: 40px;
}

/* FOOTER END */

.container {
	width: 78%;
	min-height: calc(100vh - 140px - 350px);
	margin-left: auto;
	margin-right: auto;
	display: block;
}

/* PAGE SECTION START */

.page-section {
	width: 100%;
	padding-top: 40px;
	font-size: var(--fs-2);
	user-select: none;
}

.ps-header {
	font-family: "bpg banner caps";
	text-align: center;
	letter-spacing: 2px;
}

/* PAGE SECTION END */

@media screen and (max-width: 1366px) {
	.container {
		width: 93%;
		margin-left: auto;
		margin-right: auto;
	}
}

@media screen and (max-width: 1000px) {
	.container {
		width: 73%;
	}

	.productions {
		grid-template-columns: repeat(2, 1fr);
	}

	.pp-products {
		grid-template-columns: repeat(1, 1fr);
	}
}

@media screen and (max-width: 844px) {
	.th-contact {
		display: none;
	}

	.top-header {
		padding: 10px;
	}

	.productions {
		grid-template-columns: repeat(1, 1fr);
	}

	.container {
		width: 63%;
	}

	.priority {
		grid-template-columns: repeat(1, 1fr);
	}

	.priority article {
		justify-content: start;
	}

	.pp-products {
		grid-template-columns: repeat(1, 1fr);
	}
}

@media screen and (max-width: 748px) {
	.header {
		padding-left: 20px;
	}

	.container {
		width: 93%;
	}

	.navbar {
		display: none;
	}

	.navbar-container {
		display: block;
	}

	.footer {
		grid-template-columns: repeat(1, 1fr);
	}
}
